        "description": {
            "en": "You set an alarm against intrusion. Choose a door, a window, or an area within range that is no larger than a 20-foot Cube. Until the spell ends, an alarm alerts you whenever a creature touches or enters the warded area. When you cast the spell, you can designate creatures that won't set off the alarm. You also choose whether the alarm is audible or mental:Audible Alarm. The alarm produces the sound of a handbell for 10 seconds within 60 feet of the warded area.Mental Alarm. You are alerted by a mental ping if you are within 1 mile of the warded area. This ping awakens you if you're asleep.",
            "fr": "Vous mettez en place une alarme contre les intrusions. Choisissez une porte, une fentre ou une zone  porte ne dpassant pas un Cube de 6 mtres de ct. Jusqu' la fin du sort, une alarme vous alerte ds qu'une crature touche ou pntre dans la zone protge. Lorsque vous lancez le sort, vous pouvez dsigner des cratures qui ne dclencheront pas l'alarme. Vous pouvez galement choisir si l'alarme est sonore ou mentale :Alarme sonore. L'alarme produit le son d'une clochette pendant 10 secondes, audible dans un rayon de 18 mtres autour de la zone protge.Alarme mentale. Vous tes alert par un signal mental si vous vous trouvez  moins de 1,5 km de la zone protge. Ce signal vous rveille si vous dormez."
        }
