            "fr": "Une crature que vous pouvez voir  porte doit russir un jet de sauvegarde de Charisme ou tre transporte sur un demi-plan inoffensif pendant la dure du sort. L, la cible subit l'tat Incapable d'agir.  la fin du sort, la cible rapparat dans la case qu'elle a quitte ou dans la case libre la plus proche si cette case est occupe.Si la cible est une aberration, un cleste, un lmentaire, une fe ou un filon, elle ne revient pas si le sort dure 1 minute. Elle est transporte vers un emplacement alatoire sur un plan (au choix du MD) associ  son type de crature.Emplacement de niveau suprieur. Vous pouvez cibler une crature supplmentaire pour chaque niveau d'emplacement de sort suprieur  4."
