            "en": "You conjure spirits from the Elemental Planes that flit around you in a 15-foot Emanation for the duration. Until the spell ends, any attack you make deals an extra 2d8 damage when you hit a creature in the Emanation. This damage is Acid, Cold, Fire, or Lightning (your choice when you make the attack). In addition, the ground in the Emanation is Difficult Terrain for your enemies.Using a Higher-Level Spell Slot. The damage increases by 1d8 for each spell slot level above 4*.",
            "fr": "Vous invoquez des esprits des plans lmentaires qui voltigent autour de vous dans une manation de 4,50 mtres pendant toute la dure du sort. Jusqu' la fin du sort, chaque attaque que vous effectuez inflige 2d8 dgts supplmentaires lorsque vous touchez une crature dans l'manation. Ces dgts sont d'acide, de froid, de feu ou de foudre (au choix lors de l'attaque). De plus, le sol de l'manation est un terrain difficile pour vos ennemis.Emplacement de niveau suprieur. Les dgts augmentent de 1d8 pour chaque niveau d'emplacement de sort suprieur  4."
