            "en": "You teleport to a location within range. You arrive at exactly the spot desired. It can be a place you can see, one you can visualize, or one you can describe by stating distance and direction, such as \"200 feet straight downward\" or \"300 feet upward to the northwest at a 45-degree angle.\"You can also teleport one willing creature. The creature must be within 5 feet of you when you teleport, and it teleports to a space within 5 feet of your destination space.If you, the other creature, or both would arrive in a space occupied by a creature or completely filled by one or more objects, you and any creature traveling with you each take 4d6 Force damage, and the teleportation fails.",
            "fr": "Vous vous tlportez vers un lieu  porte. Vous arrivez exactement  l'endroit souhait. Il peut s'agir d'un lieu que vous pouvez voir, visualisable, ou dcrit en indiquant la distance et la direction, par exemple  60 mtres vers le bas  ou  90 mtres vers le nord-ouest  un angle de 45 degrs .Vous pouvez galement tlporter une crature consentante. Cette crature doit se trouver dans un rayon de 1,50 mtre autour de vous lorsque vous vous tlportez, et elle se tlporte vers une case situe dans un rayon de 1,50 mtre autour de votre case de destination.Si vous, l'autre crature, ou les deux, arrivez dans une case occupe par une crature ou entirement remplie par un ou plusieurs objets, vous et toute crature voyageant avec vous subissez chacun 4d6 dgts de Force, et la tlportation choue."
