            "fr": "Une crature de votre choix que vous pouvez voir porte entend une mlodie discordante dans son esprit. La cible effectue un jet de sauvegarde de Sagesse. En cas d'chec, elle subit 3d6 dgts psychiques et doit immdiatement utiliser sa raction, si elle en a une, pour s'loigner le plus loin possible de vous, en empruntant le chemin le plus sr. En cas de russite, la cible ne subit que la moiti des dgts.Emplacement de niveau suprieur. Les dgts augmentent de 1d6 pour chaque niveau d'emplacement de sort suprieur  1."
