            "en": "This spell puts you in contact with a god or a god's servants. You ask one question about a specific goal, event, or activity to occur within 7 days. The DM offers a truthful reply, which might be a short phrase or cryptic rhyme. The spell doesn't account for circumstances that might change the answer, such as the casting of other spells.If you cast the spell more than once before finishing a Long Rest, there is a cumulative 25 percent chance for each casting after the first that you get no answer.",
            "fr": "Ce sort vous met en contact avec un dieu ou ses serviteurs. Vous posez une question concernant un objectif, un vnement ou une activit spcifique devant se produire dans les 7 prochains jours. Le MD vous donne une rponse sincre, qui peut tre une courte phrase ou une prose cryptique. Le sort ne tient pas compte des circonstances susceptibles de modifier la rponse, comme l'incantation d'autres sorts.Si vous lancez le sort plusieurs fois avant la fin d'un Repos long, vous avez 25 % de chances cumules pour chaque incantation aprs la premire de ne pas obtenir de rponse."
