            "fr": "Vous lancez un rayon d'nergie crpitante. Effectuez une attaque  distance contre une crature ou un objet  porte. En cas de succs, la cible subit 1d10 dgts de force.Amlioration de sort mineur. Le sort cre deux rayons au niveau 5, trois au niveau 11 et quatre au niveau 17. Vous pouvez diriger les rayons vers une mme cible ou vers des cibles diffrentes. Effectuez un jet d'attaque distinct pour chaque rayon."
