            "fr": "Lorsque vous touchez la cible, des lianes avides apparaissent sur elle. Elle effectue alors un jet de sauvegarde de Force. Une crature de taille G ou infrieure bnficie d'un Avantage pour ce jet de sauvegarde. En cas d'chec, la cible subit l'tat Entrav jusqu' la fin du sort. En cas de russite, les lianes se desschent et le sort prend fin.Tant qu'elle est entrave, la cible subit 1d6 dgts perforants au dbut de chacun de ses tours. La cible ou une crature  sa porte peut prendre une action pour effectuer un jet de Force (Athltisme) contre le DD de votre jet de sauvegarde de sort. En cas de russite, le sort prend fin.Emplacement de niveau suprieur. Les dgts augmentent de 1d6 pour chaque niveau d'emplacement de sort suprieur  1."
