        "materials": {
            "en": "burning incense worth 10+ GP, which the spell consumes",
            "fr": "de l'encens qui brle d'une valeur minimale de 10 po, que le sort consomme"
        },
        "duration": "Instantaneous",
        "description": {
            "en": "You gain the service of a familiar, a spirit that takes an animal form you choose: Bat, Cat, Frog, Hawk, Lizard, Octopus, Owl, Rat, Raven, Spider, Weasel, or another Beast that has a Challenge Rating of 0. Appearing in an unoccupied space within range, the familiar has the statistics of the chosen form, though it is a Celestial, Fey, or Fiend (your choice) instead of a Beast. Your familiar acts independently of you, but it obeys your commands.Telepathic Connection. While your familiar is within 100 feet of you, you can communicate with it telepathically. Additionally, as a Bonus Action, you can see through the familiar's eyes and hear what it hears until the start of your next turn, gaining the benefits of any special senses it has.Finally, when you cast a spell with a range of touch, your familiar can deliver the touch. Your familiar must be within 100 feet of you, and it must take a Reaction to deliver the touch when you cast the spell.Combat. The familiar is an ally to you and your allies. It rolls its own Initiative and acts on its own turn. A familiar can't attack, but it can take other actions as normal.Disappearance of the Familiar. When the familiar drops to 0 Hit Points, it disappears. It reappears after you cast this spell again. As a Magic action, you can temporarily dismiss the familiar to a pocket dimension. Alternatively, you can dismiss it forever. As a Magic action while it is temporarily dismissed, you can cause it to reappear in an unoccupied space within 30 feet of you. Whenever the familiar drops to 0 Hit Points or disappears into the pocket dimension, it leaves behind in its space anything it was wearing or carrying.One Familiar Only. You can't have more than one familiar at a time. If you cast this spell while you have a familiar, you instead cause it to adopt a new eligible form.",
            "fr": "Vous obtenez les services d'un familier, un esprit qui prend une forme animale de votre choix : chauve-souris, chat, grenouille, faucon, lzard, pieuvre, chouette, rat, corbeau, araigne, belette ou autre Bte dont le FD est de 0. Il apparait dans une case libre  porte et possde les statistiques de la forme choisie, mais est du type Cleste, Fe ou Filon (au choix) au lieu de Bte. Votre familier agit indpendamment de vous, mais obit  vos ordres.Lien tlpathique. Tant que votre familier est dans un rayon de 30 mtres autour de vous, vous pouvez communiquer avec lui par tlpathie. De plus, par une action Bonus, vous pouvez voir  travers les yeux du familier et entendre ce qu'il entend jusqu'au dbut de votre prochain tour, bnficiant ainsi des avantages de ses sens spciaux. Enfin, lorsque vous lancez un sort dotn la porte est de contact, votre familier peut appliquer le contact. Votre familier doit se trouver dans un rayon de 30 mtres et doit prendre une Raction pour appliquer le contact lorsque vous lancez le sort.Combat. Le familier est votre alli et celui de vos allis. Il lance sa propre Initiative et agit  son tour. Un familier ne peut pas attaquer mais peut effectuer d'autres actions normalement.Disparition du familier. Lorsque le familier tombe  0 point de vie, il disparat. Il rapparat si vous lancez ce sort  nouveau. Par une action Magie, vous pouvez renvoyer temporairement le familier dans une niche dimensionnnelle. Vous pouvez galement le renvoyer dfinitivement. Par une action Magie, tant qu'il est temporairement renvoy, vous pouvez le faire rapparatre dans un espace inoccup dans un rayon de 9 mtres. Chaque fois que le familier tombe  0 point de vie ou disparat dans la niche dimensionnelle, il laisse derrire lui tout ce qu'il portait.Un seul familier. Vous ne pouvez pas avoir plus d'un familier  la fois. Si vous lancez ce sort alors que vous avez un familier, il adopte une nouvelle forme valide."
        }
