        "description": {
            "en": "You create a 5-foot-diameter Sphere of fire in an unoccupied space on the ground within range. It lasts for the duration. Any creature that ends its turn within 5 feet of the sphere makes a Dexterity saving throw, taking 2d6 Fire damage on a failed save or half as much damage on a successful one.As a Bonus Action, you can move the sphere up to 30 feet, rolling it along the ground. If you move the sphere into a creature's space, that creature makes the save against the sphere, and the sphere stops moving for the turn.When you move the sphere, you can direct it over barriers up to 5 feet tall and jump it across pits up to 10 feet wide. Flammable objects that aren't being worn or carried start burning if touched by the sphere, and it sheds Bright Light in a 20-foot radius and Dim Light for an additional 20 feet.Using a Higher-Level Spell Slot. The damage increases by 1d6 for each spell slot level above 2.",
            "fr": "Vous crez une Sphre de feu de 1,50 mtre de diamtre dans un espace libre au sol  porte. Elle dure toute la dure du sort. Toute crature qui termine son tour dans un rayon de 1,50 mtre autour de la sphre effectue un jet de sauvegarde de Dextrit, subissant 2d6 dgts de feu en cas d'chec, ou la moiti de ces dgts en cas de russite.Par une action Bonus, vous pouvez dplacer la sphre jusqu' 9 mtres et la faire rouler au sol. Si vous dplacez la sphre dans l'espace d'une crature, cette dernire effectue son jet de sauvegarde contre la sphre, et la sphre s'immobilise pour le tour.Lorsque vous dplacez la sphre, vous pouvez la diriger par-dessus des barrires jusqu' 1,50 mtre de haut et la faire sauter par-dessus des fosses jusqu' 3 mtres de large. Les objets inflammables non ports commencent  brler au contact de la sphre, qui diffuse une Lumire vive sur un rayon de 6 mtres et une Lumire faible sur 6 mtres supplmentaires.Emplacement de niveau suprieur. Les dgts augmentent de 1d6 pour chaque niveau d'emplacement de sort suprieur  2."
        }
