            "fr": "Choisissez un objet mtallique manufactur, comme une arme en mtal ou une armure lourde ou moyenne, que vous pouvez voir  porte. Vous le faites briller d'un rougeoiement intense. Toute crature en contact physique avec l'objet subit 2d8 dgts de feu lorsque vous lancez le sort. Jusqu' la fin du sort, vous pouvez prendre une action Bonus  chacun de vos tours suivants pour infliger  nouveau ces dgts si l'objet est  porte.Si une crature tient ou porte l'objet et subit des dgts, elle doit russir un jet de sauvegarde de Constitution ou laisser tomber l'objet si elle le peut. Si elle ne le laisse pas tomber, elle subit un Dsavantage aux jets d'attaque et de caractristique jusqu'au dbut de votre prochain tour.Emplacement de niveau suprieur. Les dgts augmentent de 1d8 pour chaque niveau d'emplacement de sort suprieur  2."
