            "en": "Name or describe a famous person, place, or object. The spell brings to your mind a brief summary of the significant lore about that famous thing, as described by the DM.The lore might consist of important details, amusing revelations, or even secret lore that has never been widely known. The more information you already know about the thing, the more precise and detailed the information you receive is. That information is accurate but might be couched in figurative language or poetry, as determined by the DM.If the famous thing you chose isn't actually famous, you hear sad musical notes played on a trombone, and the spell fails.",
            "fr": "Nommez ou dcrivez une personne, un lieu ou un objet clbre. Le sort vous donne un bref rsum des informations importantes concernant cet objet, telles que dcrites par le MD.Ces informations peuvent contenir des dtails importants, des rvlations amusantes, voire des secrets inconnus du grand public. Plus vous en savez sur cet objet, plus les informations reues sont prcises et dtailles. Ces informations sont exactes, mais peuvent tre formules au moyen d'un langage figuratif ou potique, selon le choix du MD.Si l'objet clbre que vous avez choisi n'est pas rellement clbre, vous entendez des notes de musique tristes joues au trombone, et le sort choue."
