            "en": "A spectral, floating hand appears at a point you choose within range. The hand lasts for the duration. The hand vanishes if it is ever more than 30 feet away from you or if you cast this spell again.When you cast the spell, you can use the hand to manipulate an object, open an unlocked door or container, stow or retrieve an item from an open container, or pour the contents out of a vial.As a Magic action on your later turns, you can control the hand thus again. As part of that action, you can move the hand up to 30 feet.The hand can't attack, activate magic items, or carry more than 10 pounds.",
            "fr": "Une main spectrale et flottante apparat  un endroit que vous choisissez  porte. La main reste active pendant toute la dure du sort. Elle disparat si elle s'loigne de vous de plus de 9 mtres ou si vous relancez ce sort.Lorsque vous lancez ce sort, vous pouvez utiliser la main pour manipuler un objet, ouvrir une porte ou un contenant dverrouill, ranger ou rcuprer un objet d'un contenant ouvert, ou vider le contenu d'une fiole.Par une action Magie lors de vos tours ultrieurs, vous pouvez continuer de contrler la main ainsi. Cette action vous permet de dplacer la main jusqu' 9 mtres.La main ne peut pas attaquer, ni activer des objets magiques, ni porter plus de 5 kg."
