        "description": {
            "en": "You create a 10-foot-radius, 20-foot-tall Cylinder of magical energy centered on a point on the ground that you can see within range. Glowing runes appear wherever the Cylinder intersects with the floor or other surface.Choose one or more of the following types of creatures: Celestials, Elementals, Fey, Fiends, or Undead. The circle affects a creature of the chosen type in the following ways: The creature can't willingly enter the Cylinder by nonmagical means. If the creature tries to use teleportation or interplanar travel to do so, it must first succeed on a Charisma saving throw. The creature has Disadvantage on attack rolls against targets within the Cylinder. Targets within the Cylinder can't be possessed by or gain the Charmed or Frightened condition from the creature.Each time you cast this spell, you can cause its magic to operate in the reverse direction, preventing a creature of the specified type from leaving the Cylinder and protecting targets outside it.Using a Higher-Level Spell Slot. The duration increases by 1 hour for each spell slot level above 3.",
            "fr": "Vous crez un Cylindre d'nergie magique de 3 mtres de rayon et 6 mtres de haut, centr sur un point au sol que vous pouvez voir  porte. Des runes lumineuses apparaissent  chaque intersection du cylindre avec le sol ou une autre surface.Choisissez un ou plusieurs des types de cratures suivants : clestes, lmentaires, fes, filons ou morts-vivants. Le cercle affecte une crature du type choisi des manires suivantes : La crature ne peut pas entrer volontairement dans le cylindre par des moyens non magiques. Si elle tente d'utiliser la tlportation ou le voyage interplanaire pour y parvenir, elle doit d'abord russir un jet de sauvegarde de Charisme. La crature a un Dsavantage aux jets d'attaque contre les cibles situes dans le Cylindre. Les cibles situes dans le Cylindre ne peuvent pas tre possdes par la crature ni subir l'tat Charm ou Effray de sa part.Chaque fois que vous lancez ce sort, vous pouvez inverser sa magie, empchant une crature du type spcifi de quitter le Cylindre et protgeant les cibles situes  l'extrieur.Emplacement de niveau suprieur. La dure augmente d'une heure pour chaque niveau d'emplacement de sort suprieur  3."
        }
