            "fr": "Vous envoyez une dcharge d'nergie psionique dans l'esprit d'une crature que vous pouvez voir  porte. La cible effectue un jet de sauvegarde de Sagesse et subit 3d8 dgts psychiques en cas d'chec, ou la moiti en cas de russite. En cas d'chec, vous connaissez toujours l'emplacement de la cible jusqu' la fin du sort,  condition d'tre sur le mme plan d'existence qu'elle. Tant que vous avez cette connaissance, la cible ne peut pas se cacher de vous, et si elle subit l'tat Invisible, elle ne bnficie d'aucun avantage de cet tat contre vous.Emplacement de niveau suprieur. Les dgts augmentent de 1d8 pour chaque niveau d'emplacement de sort suprieur  2."
