        "description": {
            "en": "You make an area within range magically secure. The area is a Cube that can be as small as 5 feet to as large as 100 feet on each side. The spell lasts for the duration.When you cast the spell, you decide what sort of security the spell provides, choosing any of the following properties: Sound can't pass through the barrier at the edge of the warded area. The barrier of the warded area appears dark and foggy, preventing vision (including Dark vision) through it. Sensors created by Divination spells can't appear inside the protected area or pass through the barrier at its perimeter. Creatures in the area can't be targeted by Divination spells. Nothing can teleport into or out of the warded area. Planar travel is blocked within the warded area.Casting this spell on the same spot every day for 365 days makes the spell last until dispelled.Using a Higher-Level Spell Slot. You can increase the size of the Cube by 100 feet for each spell slot level above 4.",
            "fr": "Vous scurisez magiquement une zone  porte. Cette zone est un Cube dont les dimensions peuvent varier de 1,50 mtre  30 mtres de ct.Lorsque vous lancez le sort, vous dcidez du niveau de scurit qu'il offre en choisissant l'une des proprits suivantes : Le son ne peut pas traverser la barrire du primtre de la zone protge. La barrire de la zone protge apparat sombre et brumeuse, empchant toute vision (y compris par Vision dans le noir)  travers elle. Les capteurs crs par les sorts de Divination ne peuvent pas apparatre  l'intrieur de la zone protge ni traverser la barrire de son primtre. Les cratures prsentes dans la zone ne peuvent pas tre cibles par des sorts de Divination. Rien ne peut se tlporter vers ou hors de la zone protge. Un voyage interplanaire est bloqu dans la zone protge.Lancer ce sort au mme endroit chaque jour pendant 365 jours le fait durer jusqu' sa dissipation.Emplacement de niveau suprieur. Vous pouvez augmenter la taille du Cube de 30 mtres pour chaque emplacement de sort de niveau suprieur  4."
        }
