            "en": "A shimmering sphere encloses a Large or smaller creature or object within range. An unwilling creature must succeed on a Dexterity saving throw or be enclosed for the duration.Nothing-not physical objects, energy, or other spell effects can pass through the barrier, in or out, though a creature in the sphere can breathe there. The sphere is immune to all damage, and a creature or object inside can't be damaged by attacks or effects originating from outside, nor can a creature inside the sphere damage anything outside it.The sphere is weightless and just large enough to contain the creature or object inside. An enclosed creature can take an action to push against the sphere's walls and thus roll the sphere at up to half the creature's Speed. Similarly, the globe can be picked up and moved by other creatures.A Disintegrate spell targeting the globe destroys it without harming anything inside.",
            "fr": "Une sphre scintillante enferme une crature ou un objet de taille G ou infrieure  porte. Une crature rticente doit russir un jet de sauvegarde de Dextrit ou rester enferme pendant toute la dure du sort.Rien, ni des objets physiques, ni de l'nergie, ni d'autres effets de sorts, ne peut traverser la barrire, ni entrer ni sortir, bien qu'une crature dans la sphre puisse y respirer. La sphre est immunise contre tous les dgts, et une crature ou un objet  l'intrieur ne peut tre bless par des attaques ou des effets de l'extrieurs, ni endommager quoi que ce soit  l'extrieur.La sphre est lgre et juste assez grande pour contenir la crature ou l'objet qu'elle contient. Une crature enferme peut prendre une action pour pousser les parois de la sphre et ainsi faire rouler la sphre  une vitesse pouvant atteindre la moiti de sa Vitesse. De mme, le globe peut tre soulev et dplac par d'autres cratures.Un sort de dsintgration ciblant le globe le dtruit sans blesser quoi que ce soit  l'intrieur."
