            "en": "You tap into the nightmares of a creature you can see within range and create an illusion of its deepest fears, visible only to that creature. The target makes a Wisdom saving throw. On a failed save, the target takes 4d10 Psychic damage and has Disadvantage on ability checks and attack rolls for the duration. On a successful save, the target takes half as much damage, and the spell ends.For the duration, the target makes a Wisdom saving throw at the end of each of its turns. On a failed save, it takes the Psychic damage again. On a successful save, the spell ends.Using a Higher-Level Spell Slot. The damage increases by 1d10 for each spell slot level above 4.",
            "fr": "Vous sondez les cauchemars d'une crature que vous pouvez voir  porte et crez une illusion de ses peurs les plus profondes, visible uniquement par cette crature. La cible effectue un jet de sauvegarde de Sagesse. En cas d'chec, elle subit 4d10 dgts psychiques et subit un Dsavantage aux jets de caractristique et d'attaque pendant toute la dure du sort. En cas de russite, elle subit la moiti des dgts et le sort prend fin.Pendant toute la dure du sort, la cible effectue un jet de sauvegarde de Sagesse  la fin de chacun de ses tours. En cas d'chec, elle subit  nouveau les dgts psychiques. En cas de russite, le sort prend fin.Emplacement de niveau suprieur. Les dgts augmentent de 1d10 pour chaque niveau d'emplacement de sort suprieur  4."
