            "en": "You attempt to transform a creature that you can see within range into a Beast. The target must succeed on a Wisdom saving throw or shape-shift into Beast form for the duration. That form can be any Beast you choose that has a Challenge Rating equal to or less than the target's (or the target's level if it doesn't have a Challenge Rating). The target's game statistics are replaced by the stat block of the chosen Beast, but the target retains its alignment, personality, creature type, Hit Points, and Hit Point Dice.The target gains a number of Temporary Hit Points equal to the Hit Points of the Beast form. These Temporary Hit Points vanish if any remain when the spell ends*. The spell ends early on the target if it has no Temporary Hit Points left.The target is limited in the actions it can perform by the anatomy of its new form, and it can't speak or cast spells.The target's gear melds into the new form. The creature can't use or otherwise benefit from any of that equipment.",
            "fr": "Vous tentez de transformer une crature que vous pouvez voir  porte en bte. La cible doit russir un jet de sauvegarde de Sagesse ou se mtamorphoser en bte pendant toute la dure du sort. Cette forme peut tre n'importe quelle bte de votre choix dont le FP est gal ou infrieur  celui de la cible (ou  son niveau si elle n'en a pas). Les statistiques de jeu de la cible sont remplaces par le bloc de statistiques de la bte choisie, mais la cible conserve son alignement, sa personnalit, son type de crature, ses points de vie et ses ds de vie.La cible gagne un nombre de points de vie temporaires gal aux points de vie de la bte. Ces points de vie temporaires disparaissent s'il en reste  la fin du sort. Le sort prend fin prmaturment sur la cible si elle n'a plus de points de vie temporaires.Les actions de la cible sont limites par l'anatomie de sa nouvelle forme, et elle ne peut ni parler ni lancer des sorts.L'quipement de la cible se fond dans la nouvelle forme. La crature ne peut ni utiliser ni bnficier de cet quipement."
