            "fr": "Une flamme vacillante apparat dans votre main et y reste pendant toute la dure du sort. Tant qu'elle est l, elle n'met aucune chaleur, n'enflamme rien, et projette une Lumire vive sur un rayon de 6 mtres et une Lumire faible sur 6 mtres supplmentaires. Le sort prend fin si vous le relancez.Jusqu' la fin du sort, vous pouvez prendre une action magique pour lancer du feu sur une crature ou un objet dans un rayon de 18 mtres. Effectuez une attaque de sort  distance. En cas de succs, la cible subit 1d8 dgts de feu.Amlioration de sort mineur. Les dgts augmentent de 1d8 lorsque vous atteignez les niveaux 5 (2d8), 11 (3d8) et 17 (4d8)."
