            "en": "Each creature of your choice in a 5-foot-radius Sphere centered on a point within range must succeed on a Wisdom saving throw or have the Incapacitated condition until the end of its next turn, at which point it must repeat the save. If the target fails the second save, the target has the Unconscious condition for the duration. The spell ends on a target if it takes damage or someone within 5 feet of it takes an action to shake it out of the spell's effect.Creatures that don't sleep, such as elves, or that have Immunity to the Exhaustion condition automatically succeed on saves against this spell.",
            "fr": "Toute crature de votre choix dans une sphre de 1,50 mtre de rayon centre sur un point  porte doit russir un jet de sauvegarde de Sagesse ou subir l'tat Incapable d'agir jusqu' la fin de son prochain tour, aprs quoi elle doit refaire le jet. Si la cible rate le deuxime jet de sauvegarde, elle subit l'tat Inconscient pendant toute la dure du sort. Le sort prend fin si une cible subit des dgts ou si une personne dans un rayon de 1,50 mtre entreprend une action pour la librer de l'effet du sort.Les cratures qui ne dorment pas, comme les elfes, ou qui sont immunises contre l'tat puisement russissent automatiquement leurs jets de sauvegarde contre ce sort."
