            "fr": "Vous modifiez le temps pour un maximum de six cratures de votre choix dans un Cube de 12 mtres de ct  porte. Chaque cible doit russir un jet de sauvegarde de Sagesse ou tre affecte pendant toute la dure du sort.La Vitesse de la cible affecte est divise par deux, elle subit un malus de -2  la CA et  ses jets de sauvegarde de Dextrit, et elle ne peut pas prendre de ractions.  chacun de ses tours, elle peut prendre soit une action, soit une action Bonus, mais pas les deux, et elle ne peut effectuer qu'une seule attaque si elle prend l'action Attaque. Si elle lance un sort  composante somatique, il y a 25 % de chances que le sort choue car la cible effectue les gestes du sort trop lentement.Une cible affecte refait son jet de sauvegarde  la fin de chacun de ses tours, mettant fin au sort sur elle-mme en cas de russite."
