            "en": "You grant the semblance of life to a corpse of your choice within range, allowing it to answer questions you pose. The corpse must have a mouth, and this spell fails if the deceased creature was Undead when it died. The spell also fails if the corpse was the target of this spell within the past 10 days.Until the spell ends, you can ask the corpse up to five questions. The corpse knows only what it knew in life, including the languages it knew. Answers are usually brief, cryptic, or repetitive, and the corpse is under no compulsion to offer a truthful answer if you are antagonistic toward it or it recognizes you as an enemy. This spell doesn't return the creature's soul to its body, only its animating spirit. Thus, the corpse can't learn new information, doesn't comprehend anything that has happened since it died, and can't speculate about future events.",
            "fr": "Vous accordez un semblant de vie  un cadavre de votre choix  porte, lui permettant de rpondre  vos questions. Le cadavre doit avoir une bouche, et ce sort choue si la crature dcde tait un mort-vivant au moment de sa mort. Il choue galement si le cadavre a dj t la cible de ce sort au cours des 10 derniers jours.Jusqu' la fin du sort, vous pouvez poser au cadavre jusqu' cinq questions. Le cadavre ne connat que ce qu'il savait de son vivant, y compris les langues qu'il connaissait. Les rponses sont gnralement brves, cryptiques ou rptitives, et le cadavre n'est pas tenu de donner une rponse honnte si vous lui tes hostile ou s'il vous reconnat comme un ennemi. Ce sort ne rend pas l'me de la crature  son corps, seulement son esprit. Ainsi, le cadavre ne peut pas apprendre de nouvelles informations, ne comprend rien de ce qui s'est pass depuis sa mort et ne peut pas spculer sur les vnements futurs."
