            "fr": "Vous imprgnez les plantes dans une manation immobile de 9 mtres d'une conscience et d'une animation limites, leur permettant de communiquer avec vous et d'obir  des ordres simples. Vous pouvez interroger les plantes sur les vnements survenus dans la zone d'effet du sort au cours de la journe coule, obtenant ainsi des informations sur les cratures qui sont passes par l, la mto et d'autres circonstances.Vous pouvez galement transformer un Terrain difficile cr par la croissance de plantes (comme les fourrs et les sous-bois) en terrain ordinaire pendant toute la dure du sort. Vous pouvez galement transformer un terrain ordinaire o se trouvent des plantes en Terrain difficile pendant toute la dure du sort.Le sort ne permet pas aux plantes de se draciner et de se dplacer, mais elles peuvent dplacer leurs branches et leurs tiges pour vous.Si une crature du type plante se trouve dans la zone, vous pouvez communiquer avec elle comme si vous partagiez une langue commune."
