        "description": {
            "en": "You suggest a course of activitydescribed in no more than 25 wordsto one creature you can see within range that can hear and understand you. The suggestion must sound achievable and not involve anything that would obviously deal damage to the target or its allies. For example, you could say, Fetch the key to the cult's treasure vault, and give the key to me. Or you could say, Stop fighting, leave this library peacefully, and don't return.The target must succeed on a Wisdom saving throw or have the Charmed condition for the duration or until you or your allies deal damage to the target. The Charmed target pursues the suggestion to the best of its ability. The suggested activity can continue for the entire duration, but if the suggested activity can be completed in a shorter time, the spell ends for the target upon completing it.",
            "fr": "Vous suggrez une activit (dcrite en 25 mots maximum)  une crature que vous pouvez voir  porte, capable de vous entendre et de vous comprendre. La suggestion doit paratre ralisable et ne pas impliquer d'actions susceptibles d'infliger des dgts  la cible ou  ses allis. Par exemple, vous pourriez dire  Va chercher la cl du coffre du trsor du culte et donne-la-moi ou encore  Arrte de te battre, quitte cette bibliothque paisiblement et ne revient pas .La cible doit russir un jet de sauvegarde de Sagesse ou subir l'tat Charm pendant toute la dure du sort, ou jusqu' ce que vous ou vos allis lui infligiez des dgts. La cible charme suit la suggestion du mieux qu'elle peut. L'activit suggre peut se poursuivre pendant toute la dure du sort, mais si elle peut tre ralise plus rapidement, le sort prend fin pour la cible ds sa ralisation."
        }
