        "description": {
            "en": "A nonmagical wall of solid stone springs into existence at a point you choose within range. The wall is 6 inches thick and is composed of ten 10-foot-by-10-foot panels. Each panel must be contiguous with another panel. Alternatively, you can create 10-foot-by-20-foot panels that are only 3 inches thick.If the wall cuts through a creature's space when it appears, the creature is pushed to one side of the wall (you choose which side). If a creature would be surrounded on all sides by the wall (or the wall and another solid surface), that creature can make a Dexterity saving throw. On a success, it can use its Reaction to move up to its Speed so that it is no longer enclosed by the wall.The wall can have any shape you desire, though it can't occupy the same space as a creature or object. The wall doesn't need to be vertical or rest on a firm foundation. It must, however, merge with and be solidly supported by existing stone. Thus, you can use this spell to bridge a chasm or create a ramp.If you create a span greater than 20 feet in length, you must halve the size of each panel to create supports. You can crudely shape the wall to create battlements and the like.The wall is an object made of stone that can be damaged and thus breached. Each panel has AC 15 and 30 Hit Points per inch of thickness, and it has Immunity to Poison and Psychic damage. Reducing a panel to 0 Hit Points destroys it and might cause connected panels to collapse at the DM's discretion.If you maintain your Concentration on this spell for its full duration, the wall becomes permanent and can't be dispelled. Otherwise, the wall disappears when the spell ends.",
            "fr": "Un mur de pierre solide et non magique apparat  l'endroit que vous choisissez et  porte. Ce mur a une paisseur de 15 cm et est compos de dix panneaux de 3 mtres sur 3 mtres. Chaque panneau doit tre contigu  un autre. Vous pouvez galement crer des panneaux de 3 mtres sur 6 mtres de seulement 7,50 cm d'paisseur.Si le mur traverse l'espace d'une crature lors de son apparition, celle-ci est repousse d'un ct du mur (selon votre choix). Si une crature est entoure de tous cts par le mur (ou par le mur et une autre surface solide), elle peut effectuer un jet de sauvegarde de Dextrit. En cas de russite, elle peut utiliser sa raction pour se dplacer jusqu' sa Vitesse et ainsi sortir du mur.Le mur peut prendre la forme de votre choix, mais il ne peut pas occuper le mme espace qu'une crature ou un objet. Il n'est pas ncessaire qu'il soit vertical ni qu'il repose sur des fondations solides. Il doit cependant fusionner avec de la pierre existante et tre solidement soutenu par elle. Vous pouvez donc utiliser ce sort pour combler un gouffre ou crer une rampe.Si vous crez une porte suprieure  6 mtres de long, vous devez diviser par deux la taille de chaque panneau pour crer des supports. Vous pouvez faonner grossirement le mur pour crer des crneaux, etc.Le mur est un objet en pierre qui peut tre endommag et donc perc. Chaque panneau a une CA de 15, 30 points de vie par tranche de 2,50 cm d'paisseur, et est immunis contre le poison et les dgts psychiques. Rduire un panneau  0 point de vie le dtruit et peut provoquer l'effondrement des panneaux connects,  la discrtion du MD.Si vous maintenez votre concentration sur ce sort pendant toute sa dure, le mur devient permanent et ne peut tre dissip. Sinon, il disparat  la fin du sort."
        }
